.journey{
    background-color: rgb(255, 255, 255);
    display: flex;
    flex-direction: column;
    color: rgb(61, 61, 61);
}
.journeytitle >h1{
    font-size: 40px;
}
.journeyentry{
    display: flex;
    min-height: 100px;
    justify-content: center;
    align-items: center;
    padding: 25px 0px;
}
.entrytext{
    height: 100%;
    flex: 3 1 0;
    margin: 0px 20px;
    padding: 5px 5px;
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.journeyicon{
    flex: 1 1 0;
    display: flex;
    justify-content: center;
}

.iconimg{
    height: calc(7vw + 5vh);
    transition: 0.3s;
}
.iconimg:hover{
    height: calc(8vw + 6vh);
}
.invert{
    background-color: rgb(187, 187, 255);
}
.left{
    align-items: flex-start;
}
.right{
    align-items: flex-end;
}
.subtitle{
    flex: 1 1 0;
    font-style: italic;
    font-size: calc(0.38vw + 0.38vh + 0.2vmin);
    color:rgb(109, 109, 109);
}

.maintext{
    display: flex;
    align-items: center;
    flex: 3 1 0;
    color: white;
    font-size: calc(0.4vw + 0.4vh + 0.2vmin);
}
.uninvert{
    font-size: calc(0.66vw + 0.66vh + 0.3vmin);
}
