.socials{
    background-color: rgb(255, 248, 248);
    display: flex;
    flex-direction: column;
    color:rgb(49, 49, 49);
    padding: 20px;
}
.socialicons{
    flex-wrap: wrap;
    justify-content: center;
    display: flex;
    padding: 20px;
}

.socialicon{
    width: 130px;
    margin: 25px 25px;
    transition: 0.3s;
}
.socialicon:hover{
    transform: scale(1.1);
}
.socials > h2{
    font-style: italic;
}
.socials > h1{
    font-size: calc(2vw + 2vh + 1vmin);
}