.achievements{
    background-color: rgb(251, 251, 255);
    display: flex;
}
.items{
    flex: 2 1 0;
    padding: 4%;
    text-align: left;
}
.display{
    flex: 5 1 0;
    padding: 20px;
}
.achievementcontent{
    background-color: rgb(225, 225, 255);
    height:100%;
    border-radius: 20px;
    display: flex;
    align-items: center;
    height: 100%;
}
.itemname{
    padding: 10px 10px 10px 10px;
    margin: 2vh 0px 2vh 20px;
    cursor: pointer;
    border-radius: 10px;
    background-color:inherit;
    font-size: calc(1vw + 1vh + 0.5vmin);
    color: rgb(51, 51, 51);
    transition: 0.3s;
}
.achievementtitle{
    font-size: calc(1.75vw + 1.75vh + 0.8vmin);
}
.achievementtitlewrapper{
    display:flex;
    justify-content: left;
    align-items: center;
}
.achievementtitlewrapper >div{
    flex: 1;
    height: inherit;
}
.expandbutton{
    justify-self: right;
    background-color:inherit;
    display: none;
    flex: 1 1 0;
    height: 60px;
    background-color: rgb(251, 251, 255);
    float:right;
}
.achievementimg{
    flex: 2 1 0;
    padding: 20px;
}
.achievementimg>img{
    width: 30vw;
}
  .achievementtext{
    flex: 3 1 0;
    padding: 12px;
  }

  @media screen and (max-width: 1024px){

    .achievementimg>img{
        width: 50vw;
    }
    .achievements{
        display: inline;
    }
    .expandbutton{
        display: inline;
        cursor: pointer;
    }
    .itemname{
        margin: 1vh 0px 1vh 20px;
    }
    .hide{
        display: none;
    }
    .achievementcontent{
        display: block;
    }
  }
