.aboutme{
    display: flex;
    background-color: rgb(250, 250, 250);
}
.aboutmetext{
    text-align: left;
    display: flex;
    flex-direction: column;
    padding: 20px 50px;
}
.aboutmetext > div{
    padding: 10px 0px;
}
.answer{
    font-size: calc(0.9vw + 0.9vh + 0.5vmin);
    padding: 0px 10px;
    color: rgb(85, 85, 85);
}
.question{
    font-size: calc(1.5vw + 1.5vh + 0.75vmin);
    font-style: italic;
    color: rgb(255, 187, 187);
}
.aboutmeimg{
    background-position: 20% 20%;
    background-repeat: no-repeat;
}
@media screen and (max-width: 800px) {
    .aboutmeimg {
        display: none !important;
    }
    .aboutmetext{
        width:100%;
    }
}
@media screen and (min-width: 800px) {
    .aboutme > div{
        width: 50%;
    }
}